"use client";

import { useEffect, useState } from "react";
import { API_URL } from "../api/apiURL";
import jsPDF from "jspdf";
import Image from "next/image";

export default function UsersPage() {
  const [search, setSearch] = useState("");
  const [statusFilter, setStatusFilter] = useState("");
  const [genderFilter, setGenderFilter] = useState("");
  const [users, setUsers] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [loading, setLoading] = useState(false);

  const limit = 10;

  const fetchUsers = async () => {
    try {
      setLoading(true);

      const params = new URLSearchParams({
        search,
        status: statusFilter,
        gender: genderFilter,
        page: currentPage,
        limit,
      });

      const response = await fetch(`${API_URL}/admin/getUser?${params.toString()}`);
      const data = await response.json();

      setUsers(data.data || []);
      setTotalPages(data.totalPages || 1);
      setLoading(false);
    } catch (error) {
      console.error("Error:", error);
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchUsers();
  }, [search, statusFilter, genderFilter, currentPage]);

  /* ---------------- PAGINATION WINDOW LOGIC ---------------- */

  const windowSize = 4;
  const startPage = Math.floor((currentPage - 1) / windowSize) * windowSize + 1;
  const endPage = Math.min(startPage + windowSize - 1, totalPages);

  const changePage = (page) => {
    if (page >= 1 && page <= totalPages) setCurrentPage(page);
  };

  return (
    <div className="p-6 max-w-full mx-[-12px]">
      <div className="bg-white rounded-xl shadow border border-gray-400 p-4">

        {/* ------- SEARCH + FILTERS ------- */}
        <div className="flex flex-wrap justify-between items-center mb-4 gap-4">

          {/* SEARCH BOX */}
          <div className="flex items-center gap-2 px-4 py-2 shadow border rounded-xl bg-white w-[300px]">
            <Image src="/search.png" width={20} height={20} alt="Search" />
            <input
              type="text"
              placeholder="Search By User ID"
              className="border-none w-full focus:outline-none"
              value={search}
              onChange={(e) => {
                setSearch(e.target.value);
                setCurrentPage(1);
              }}
            />
          </div>

          <div className="flex gap-2 flex-wrap">

            {/* STATUS FILTER */}
            <select
              className="border bg-gray-200 px-3 py-2 rounded"
              value={statusFilter}
              onChange={(e) => {
                setStatusFilter(e.target.value);
                setCurrentPage(1);
              }}
            >
              <option value="">Status</option>
              <option>Approved</option>
              <option>Pending</option>
              <option>Blocked</option>
            </select>

            {/* GENDER FILTER */}
            <select
              className="border bg-gray-200 px-3 py-2 rounded"
              value={genderFilter}
              onChange={(e) => {
                setGenderFilter(e.target.value);
                setCurrentPage(1);
              }}
            >
              <option value="">Gender</option>
              <option>Male</option>
              <option>Female</option>
            </select>

            <button
              className="bg-gray-200 px-4 py-2 rounded hover:bg-gray-100"
            >
              Export CSV
            </button>
          </div>
        </div>

        {/* ------- TABLE ------- */}
        <div className="overflow-auto">
          <table className="min-w-full text-sm border-t border-gray-400">
            <thead className="bg-gray-100">
              <tr>
                {[
                  "User ID",
                  "User Name",
                  "Location",
                  "Gender",
                  "Joined",
                  "Verified",
                  "Status",
                  "Last Active",
                ].map((head) => (
                  <th
                    key={head}
                    className="text-left px-3 py-2 border-b border-gray-400"
                  >
                    {head}
                  </th>
                ))}
              </tr>
            </thead>

            <tbody>
              {loading ? (
                <tr>
                  <td colSpan={8} className="text-center py-4">
                    <Image src="/loading2.gif" width={100} height={100} alt="loading" />
                  </td>
                </tr>
              ) : users.length > 0 ? (
                users.map((user, idx) => (
                  <tr
                    key={idx}
                    className="border-b border-gray-300 hover:bg-gray-50"
                  >
                    <td className="px-3 py-2">{user.id}</td>
                    <td className="px-3 py-2">{user.name}</td>
                    <td className="px-3 py-2">{user.location}</td>
                    <td className="px-3 py-2">{user.gender}</td>
                    <td className="px-3 py-2">{user.joined}</td>

                    {/* VERIFIED ICON (✔ / ✘) */}
                    <td className="px-3 py-2">
                      {user.verified === "Yes" ? (
                        <span className="flex items-center gap-1 text-green-600">
                          <span className="text-lg">✔</span> Yes
                        </span>
                      ) : (
                        <span className="flex items-center gap-1 text-red-600">
                          <span className="text-lg">✘</span> No
                        </span>
                      )}
                    </td>

                    {/* STATUS WITH COLOR DOT */}
                    <td className="px-3 py-2 capitalize">
                      <span className="flex items-center gap-2">
                        <span
                          className={`w-3 h-3 rounded-full ${
                            user.status === "Approved"
                              ? "bg-green-500"
                              : user.status === "Pending"
                              ? "bg-yellow-400"
                              : "bg-red-500"
                          }`}
                        ></span>
                        {user.status}
                      </span>
                    </td>

                    <td className="px-3 py-2">{user.lastActive}</td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan={8} className="text-center py-4 text-gray-500">
                    No users found.
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>

       
        {/* ------- PAGINATION (Exact Screenshot) ------- */}
        <div className="flex justify-center items-center gap-2 mt-6 text-sm text-gray-800">

          {/* PREV */}
          <button
            onClick={() => changePage(currentPage - 1)}
            disabled={currentPage === 1}
            className={`${currentPage === 1 ? "text-gray-400" : "hover:underline"} flex items-center gap-1`}
          >
            ◄ Prev
          </button>

          <span>|</span>

          {/* PAGE NUMBERS */}
          {Array.from({ length: endPage - startPage + 1 }, (_, i) => startPage + i)
            .map((page, idx, arr) => (
              <div key={page} className="flex items-center gap-2">
                <button
                  onClick={() => changePage(page)}
                  className={`${
                    page === currentPage
                      ? "font-bold underline text-black"
                      : "hover:underline"
                  }`}
                >
                  {page}
                </button>

                {/* | after each except last */}
                {idx !== arr.length - 1 && <span>|</span>}
              </div>
            ))}

          {/* DOTS */}
          {endPage < totalPages && <span>.....</span>}

          <span>|</span>

          {/* NEXT */}
          <button
            onClick={() => changePage(currentPage + 1)}
            disabled={currentPage === totalPages}
            className={`${currentPage === totalPages ? "text-gray-400" : "hover:underline"} flex items-center gap-1`}
          >
            Next ►
          </button>
        </div>
      </div>
    </div>
  );
}
